body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background-image: url(/my-portfolio/static/media/DSC01303.91e4d5c1.jpg);
  background-size: cover;
}

.App {
  text-align: center;
  background: linear-gradient(45deg, rgba(142, 255, 217, 0.7), rgb(66, 179, 255));
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(18px + 2vmin);
  font-family: 'Black Ops One', cursive; 
  color: white;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
  
}

.App-header {
  text-align:center;
}

.App-link {
  color: #61dafb;
}

.demo0-block {
  position: absolute;
  width: 60px;
  height: 60px;
  top: 50px;
  border-radius: 100px;
  background: linear-gradient(180deg, #a95dff, rgba(0, 153, 255, 0.925));
  color: white;
  font-size: 15px;
  outline: 0;
}

.panel{
  background-color: rgba(255, 255, 255, 0.5);
  width: 80%;
  height: 80vh;
  margin-top: 100px
}

.panel-title{
  -webkit-transform: translate3d(0, 0, 1px);
  transform: translate3d(0, 0, 1px);
  
}

h2{
  color : transparent;
  background: -webkit-linear-gradient(-45deg, rgba(122, 0, 0, 0.7), rgba(255, 44, 107, 0.7));
  background-clip: text;
  -webkit-background-clip: text;
  opacity: 1;
  transition: 1s;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

